<template>
  <div class="companyRight">
      <h1>{{companys[this.ids].h1}}</h1>
    <div class="companyRight-item" v-for="item in companys[this.ids].children" :key="item.h4">
        <h4>{{item.h4}}</h4>
        <p>{{item.p}}</p>
    </div>
  </div>
</template>

<script>
import { companys } from "~/data/index";

export default {
  data() {
    return {
      ids: null,
      companys: companys
    };
  },
  created() {
    this.ids = this.$route.params.id;
    window.scrollTo(0, 0);
  },
  components: {}
};
</script>

<style scoped lang="scss">
.companyRight {
  padding: 30px;
  h1 {
    font-size: 24px;
    font-weight: bold;
    margin-top: 25px;
  }
  h1::after {
    content: "\0020";
    width: 14px;
    height: 6px;
    background-color: #e60012;
    display: block;
    margin: 24px 0 50px 0;
  }
  .companyRight-item {
    h4 {
      font-size: 20px;
      color: #112233;
      font-weight: 600;
      margin-top: 50px;
    }
    p {
      line-height: 1.8;
      text-align: justify;
      font-weight: 300;
      margin-top: 25px;
    }
  }
}
</style>



